import React, {FC} from 'react'
import { useState, useEffect, useRef } from 'react';
import './collapse.css'
// import iconDown from '../Assets/icondown.png'
// import iconUp from '../Assets/iconup.png'


interface CollpaseProps {
    open?: boolean;
    header: string | React.ReactNode;
    headerClassName?: string;
    titleClassName?: string;
    iconButtonClassName?: string;
    contentClassName?: string;
    contentContainerClassName?: string;
    collapsibleClassName?: string;
}


export const Collapse = ({
    open,
    collapsibleClassName = "collapsible-card-edonec",
    headerClassName = "collapsible-header-edonec",
    titleClassName = "title-text-edonec",
    iconButtonClassName = "collapsible-icon-button-edonec",
    contentClassName = "collapsible-content-edonec",
    contentContainerClassName = "collapsible-content-padding-edonec",
    children,
    header

}: CollpaseProps) => {
    const [isOpen, setIsOpen] = useState(open);
    const [height, setHeight] = useState <number | undefined>(open ? undefined : 0)
    const handleFilterOpening = () => {
        setIsOpen((prev) => !prev);
        const ref = useRef<HTMLDivElement>(null);
        const handleFilterOpening = () => {
            setIsOpen((prev) => !prev);
          };
          useEffect(() => {
            if (!height || !isOpen || !ref.current) return undefined;
            // @ts-ignore
            const resizeObserver = new ResizeObserver((el) => {
              setHeight(el[0].contentRect.height);
            });
            resizeObserver.observe(ref.current);
            return () => {
              resizeObserver.disconnect();
            };
          }, [height, isOpen]);
        useEffect(() => {
            if (isOpen) setHeight(ref.current?.getBoundingClientRect().height);
            else setHeight(0);
          }, [isOpen]);
    }


    return (
        <>
            <div className="card">
                <div>
                    <div className="collapse-size">
                        <h6 className="text-weight">{title}</h6>
                        <button type="button" className="btn" onClick={handleFilterOpening}>
                            {!isOpen ? (
                                // <img src={iconDown} alt="" />
                                <span>↓</span>
                                ) : (
                            //    <img src={iconUp} alt="" />
                            <span>↑</span>
                            )}
                        </button>
                    </div>
                </div>

                <div className="opened_cont">
                    <div>{isOpen && <div className="container_opened_content">{children}</div>}</div>
                </div>
            </div>
        </>
    )
}

export default Collapse